  z-index: 2;
  overflow: hidden;
  background: #ffffff;
}

.main:before, .main:after {
  content: " ";
  display: table;
}
.main:after {
  display: block;
  clear: both;
  height: 1px;
  margin-top: -1px;
  visibility: hidden;
}
.main {
  *zoom: 1;
}
.fadein {
  opacity: 0;
  visibility: hidden;
}

.inview {
  opacity: 0;
  visibility: hidden;
}
.inview.fadein {
  transition: opacity 0.6s ease-in, visibility 1s ease-in;
}
.inview.fadeinslow {
  transition: opacity 0.8s ease-in, visibility 1s ease-in;
}
.inview.fadeinxslow {
  transition: opacity 5.4s ease-in, visibility 5.8s ease-in;
}
.inview.fadeinup {
  position: relative;
  transform: translateY(30px);
  transition: transform 0.6s ease-in, opacity 0.6s ease-in, visibility 1s ease-in;
}
.inview.fadeindown {
  position: relative;
  transform: translateY(-30px);
  transition: transform 0.6s ease-in, opacity 0.6s ease-in, visibility 1s ease-in;
}
.inview.fadeinleft {
  position: relative;
  transform: translateX(-60px);
  transition: transform 0.8s ease-in, opacity 0.8s ease-in, visibility 1s ease-in;
}
.inview.fadeinright {
  position: relative;
  transform: translateX(60px);
  transition: transform 0.8s ease-in, opacity 0.8s ease-in, visibility 1s ease-in;
}
.inview.over {
  opacity: 1;
  visibility: visible;
}
.inview.over.fadeindown {
  transform: translateY(0);
}
.inview.over.fadeinup {
  transform: translateY(0);
}
.inview.over.fadeinleft {
  transform: translateX(0);
}
.inview.over.fadeinright {
  transform: translateX(0);
}
.inview.over.fadein {
  opacity: 1;
  visibility: visible;
}

